  margin:0 0.3em 0.3em 0;
  border-radius:2em;
  box-sizing: border-box;
  text-decoration:none;
  font-weight:300;
  color:rgb(255, 255, 255);
  background-color: rgba(28, 85, 158, 0.800);
  text-align:center;
  transition: all .1s; 
  font-size: 14px;
  box-shadow: 2px 2px 10px black;
